.myOrdersPage{
    width: 100vw;
    max-width: 100%;
    padding: 0 7vmax;
    box-sizing: border-box;
    background-color: rgb(235,235,235);
    position: fixed;
    display: flex;
    height: 100vh;
    top: 0;
    left: 0;
    flex-direction: column;
}


#myOrdersHeading{
    text-align: center;
    background-color: rgb(44,44,44);
    padding: 0.5vmax;
    box-sizing: border-box;
    color: rgb(255,255,255);
    transition: all 0.5s;
    font: 400 1.2vmax "Roboto";
}

.myOrdersTable{
    background-color: white;
}

.myOrdersTable div{
    font: 300 1vmax "Roboto";
    color: rgba(0,0,0,0.678);
    border: none;
}

.myOrdersTable a{
    color: rgba(0,0,0,0.527);
    transition: all 0.5s;
}

.myOrdersTable a:hover{
    color: tomato;
}

.MuiDataGrid-columnsContainer{
    background-color: tomato;
    padding: 1vmax !important;
}

.MuiDataGrid-columnsContainer div{
    color: rgb(255,255,255);
    font: 500 1.1vmax "Roboto" !important;
}

.MuiDataGrid-iconSeparator{
    display: none !important;
}

@media screen and (max-width:600px) {
    .myOrdersPage{
        padding: 0;
        height: 93vh;
    }
    #myOrdersHeading{
        font: 400 2.2vmax "Roboto";
        padding: 4vw;
    }

    .myOrdersTable div{
        font: 300 4vw "Roboto";
    }

    .MuiDataGrid-columnsHeader{
        padding: 20px !important;
    }

    .MuiDataGrid-columnHeader div {
        font: 500 5vw "Roboto" !important;
    }
}