.confirmOrderPage{
    height: 100vh;
    background-color: white;
    display: grid;
    grid-template-columns: 6fr 3fr;
}

.confirmOrderPage > div:last-child{
    border-left: 1px solid rgba(0,0,0,0.247);
}

.confirmshippingArea{
    padding: 5vmax;
    padding-bottom: 0%;
}

.confirmshippingArea > p,.confirmCartItems > p{
    font: 400 1.8vmax "Roboto";
}

.confirmshippingAreaBox , .confirmCartItemsContainer{
    margin: 2vmax;
}

.confirmshippingAreaBox > div{
    display: flex;
    margin: 1vmax 0;
}

.confirmshippingAreaBox > div > p{
    font: 400 1vmax "Roboto";
    color: black;
}

.confirmshippingAreaBox > div > span{
    color: #575757;
    margin: 0 1vmax;
    font: 100 1vmax "Roboto";
}

.confirmCartItems {
    padding: 5vmax;
    padding-top: 2vmax;
}

.confirmCartItemsContainer{
    max-height: 20vmax;
    overflow-y: auto;
}

.confirmCartItemsContainer > div{
    font: 400 1vmax "Roboto";
    display: flex;
    justify-content: space-between;
    margin: 2vmax 0;
    align-items: center;
}

.confirmCartItemsContainer > div > img{
    width: 3vmax;
}

.confirmCartItemsContainer > div > p{
    width: 60%;
    text-decoration: none;
    color: #575757;
    margin: 0 2vmax;
}

.confirmCartItemsContainer > div >span{
    font: 100 1vmax "Roboto";
    color: #5e5e5e;
}

.orderSummary{
    padding: 7vmax;
}

.orderSummary > p{
    text-align: center;
    box-sizing: border-box;
    border-bottom: 1px solid rgba(0,0,0,0.267);
    padding: 1vmax;
    width: 100%;
    margin: auto;
    box-sizing: border-box;
}

.orderSummary > div > div{
    display: flex;
    margin: 2vmax 0;
    justify-content: space-between;
    font: 300 1vmax "Roboto";
}

.orderSummary > div >div >span{
    color:rgba(0,0,0,0.692);
}

.orderSummaryTotal{
    display: flex;
    padding: 2vmax 0;
    justify-content: space-between;
    font: 300 1vmax "Roboto";
    border-top: 1px solid rgba(0,0,0,0.363);
}

.orderSummary >button{
    background-color: tomato;
    transition: all 0.5s;
    font: 400 1vmax "Roboto";
    color:white;
    width: 100%;
    cursor: pointer;
    padding: 1vmax;
    border: none;
    margin: auto;
}


.orderSummary >button:hover{
    background-color: rgb(192,71,50);
}

@media screen and (max-width:600px){
    .confirmOrderPage{
        grid-template-columns: 1fr;
        height: unset;
    }

    .confirmOrderPage > div:last-child{
        border-left:0;
        border-top: 1px solid rgba(0,0,0,0.247);
    }
    .confirmshippingArea > p{
        font: 400 6vw "Roboto";
    }
    .confirmshippingAreaBox > div{
        display: flex;
        margin: 6vw 0;
    }

    .confirmshippingAreaBox > div > p{
        font: 400 4vw "Roboto";
    }
    .confirmshippingAreaBox > div > span{
        font: 400 4vw "Roboto";
    }
    .confirmCartItems > p{
        font: 400 6vw "Roboto";
    }

    .confirmCartItemsContainer{
        max-height: 50vw;
    }
    .confirmCartItemsContainer > div{
        font: 400 4vw "Roboto";
        margin: 4vw 0;
    }

    .confirmCartItemsContainer > div > img{
        width: 10vw;
    }
    .confirmCartItemsContainer >div >a{
        margin: 0;
        width: 30%;
    }
    .confirmCartItemsContainer >div >span{
        font: 100 4vw "Roboto";
    }
    .orderSummary{
        padding: 12vw;
    }
    .orderSummary > p{
        font: 400 6vw "Roboto";
        padding: 4vw;
    }
    .orderSummary > div>div{
        font: 300 4vw "Roboto";
    }
    .orderSummaryTotal{
        font: 300 4vw "Roboto";
        padding:5vw 0 ;
    }
    .orderSummary>button{
        padding: 4vw;
        margin: 4vw auto;
        font: 400 4vw "Roboto"; 
    }
}


