.shippingContainer{
    width:100vw;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    max-width: 100%;
    display: flex;
}

.shippingBox{
    width: 25vw;
    background-color: white;
    overflow: hidden;
    box-sizing: border-box;
    height: 90vh;
}

.shippingHeading{
    text-align: center;
    color: rgba(0,0,0,0.664);
    width: 50%;
    margin: auto;
    padding: 1.3vmax;
    font: 400 1.3vmax "Roboto";
    border-bottom: 1px solid rgba(0,0,0,0.205);
}

.shippingForm{
    display: flex;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    height: 80%;
    transition:all 0.5s;
    flex-direction: column;
}

.shippingForm > div{
    display: flex;
    width: 100%;
    align-items: center;
}

.shippingForm > div > input,.shippingForm > div > select{
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.267);
    border-radius:4px;
    font: 300 0.9vmax cursive;
    outline: none;
}

.shippingForm > div > svg{
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0,0,0,0.623);
}

.shippingBtn{
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1vmax "Roboto";
    width: 100%;
    padding: 1vmax;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
    margin: 2vmax;
}

.shippingBtn:hover{
    background-color: rgb(179,66,46);
}

@media screen and (max-width:600px) {
    .shippingBox{
        width: 100vw;
        height: 95vh;
    }
    .shippingHeading{
        font: 400 6vw "Roboto";
        padding: 5vw;
    }    
    .shippingForm{
        padding: 11vw;
    }
    .shippingForm > div > input , .shippingForm > div > select{
        padding: 5vw 10vw;
        font: 300 4vw cursive;
    }
    .shippingForm > div > svg{
        font-size: 6vw;
        transform: translateX(3vw);
    }
    .shippingBtn{
        font: 300 4vw "Roboto";
        padding: 4vw;
    }
}
